@import '../../scss/variables.scss';

.hamburgerBtn {
  position: absolute;
  top: 0;
  left: 0;
  background: none;
  width: 32px;
  height: 32px;

  margin: 32px 16px;
  stroke: $color-white;

  @media screen and (max-width: $mobile) {
    margin: 16px;
    stroke: $color-black;
  }
}
